import React, { Component } from 'react';
import T from 'prop-types';
import { pick } from 'lodash';
import DockingTabBar, { Tab } from '@splunk/react-docs/DockingTabBar';
import APIPage from '@splunk/react-docs/API';
import previewVizMessage from '@splunk/visualizations-shared/previewVizMessage';

class ComponentPage extends Component {
    static currentSection = 'overview';

    static propTypes = {
        createComponentURL: T.func,
        dependsOnSplunk: T.bool,
        /**
         * If status is `preview`, a message is prepended to the description reflecting this visualization is new
         *  and minor changes may be made soon.
         */
        status: T.string,
        description: T.string,
        /**
         * The "overview" section of the doc page.
         */
        overviewSection: T.node,
        /**
         * The "examples" section of the doc page.
         */
        examplesSection: T.node.isRequired,
        /**
         * The "options" section of the doc page.
         */
        optionSection: T.node.isRequired,
        /**
         * The "encoding" section of the doc page.
         */
        encodingSection: T.node,
        /**
         * The "event" section of the doc page.
         */
        eventSection: T.node,
        /**
         * The "theme" section of the doc page
         */
        themeSection: T.node,
        headerOffset: T.number,
        /**
         * Overrides the (otherwise automatically generated) source code url.
         */
        sourceURL: T.string,
        title: T.string.isRequired,
    };

    static defaultProps = {
        createComponentURL() {},
        headerOffset: 0,
    };

    constructor(...args) {
        super(...args);
        this.state = { activeSection: ComponentPage.currentSection };
    }

    handleBodyMount = el => {
        this.bodyEl = el;
    };

    handleSectionChange = (e, { selectedTabId }) => {
        ComponentPage.currentSection = selectedTabId;
        this.setState({ activeSection: selectedTabId }, () => {
            // After the new section loads, scroll to the top of the section.
            const { top } = this.bodyEl.getBoundingClientRect();
            if (top < window.scrollY) {
                const target = top + window.scrollY - 90;
                window.scroll(0, target);
            }
        });
    };

    renderBody() {
        const {
            encodingSection,
            optionSection,
            overviewSection,
            examplesSection,
            eventSection,
            themeSection,
        } = this.props;
        const { activeSection } = this.state;
        switch (activeSection) {
            case 'overview':
                return overviewSection;
            case 'examples':
                return examplesSection;
            case 'encoding':
                return encodingSection;
            case 'option':
                return optionSection;
            case 'event':
                return eventSection;
            case 'theme':
                return themeSection;
            default:
                return null;
        }
    }

    render() {
        const {
            createComponentURL,
            overviewSection,
            examplesSection,
            encodingSection,
            eventSection,
            themeSection,
            headerOffset,
            sourceURL,
        } = this.props;
        const basePageProps = pick(this.props, 'dependsOnSplunk', 'description', 'title');

        if (this.props.status === 'preview') {
            basePageProps.description = `#### ${previewVizMessage} \n ${basePageProps.description || ''}`;
        }

        let { activeSection } = this.state;
        let body = this.renderBody();

        if (!body) {
            body = examplesSection;
            activeSection = 'examples';
        }
        return (
            <APIPage sourceURL={sourceURL || createComponentURL(window.location.pathname)} {...basePageProps}>
                <DockingTabBar
                    activeTabId={activeSection}
                    offset={headerOffset}
                    onChange={this.handleSectionChange}
                >
                    {overviewSection && <Tab label="Overview" tabId="overview" />}
                    <Tab label="Examples" tabId="examples" />
                    {encodingSection && <Tab label="Encoding" tabId="encoding" />}
                    <Tab label="Options" tabId="option" />
                    {eventSection && <Tab label="Events" tabId="event" />}
                    {themeSection && <Tab label="Themes" tabId="theme" />}
                </DockingTabBar>
                <div
                    style={{
                        padding: '0 30px 30px',
                        marginTop: '20px',
                    }}
                    ref={this.handleBodyMount}
                >
                    {body}
                </div>
            </APIPage>
        );
    }
}

export default ComponentPage;
